@import url(https://fonts.googleapis.com/css2?family=Pattaya&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pattaya&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}




.nav {
    position: absolute;
    top: 0;
} 

.logout {
    line-height: 45px; 
    font-size: 24px;
    font-family: Pattaya;
    float: right;
}

.emoji {
    border-radius: 50%;
    background: #499ae6;
    width: 45px;
    height: 45px;
    line-height: 45px; 
    font-size: 24pt;
    float: right;
    margin-left: 5px;
}

.title {
    letter-spacing: 5px;
    line-height: 45px; 
    font-family: Pattaya;
    font-size: 26px;
    color: rgb(211, 232, 250);
    text-shadow: -4px 0  #57709c, 0 3px  #57709c, 2px 0  #57709c, 0 -4px  #57709c;
    margin-left: auto;
}

.yass-title {
    float:left;
}

.or-title {
    float:left;
    font-size: 15px;
    text-shadow: -2px 0  #57709c, 0 1px  #57709c, 1px 0  #57709c, 0 -2px  #57709c;
}
.pass-title {
    float:left;
}
.feed-container {
    background-color: rgb(109, 141, 182);
    min-height: 100%;
    height: auto !important;
    height: 100%;
    margin: 0 auto -142px;
}
  
.vibe {
    width: 35%;
    float: right;
    border-radius: 25px;
