}

.post-container {
    width: 60%;
    margin-left: 10px;
    margin-right: 10px;
    background-color: rgb(109, 141, 182);
}

.posts {
    border-radius: 25px;
    border-width: 1px;
    border-style: solid;
    border-color: whitesmoke;
